import React, { Component } from 'react';
import logo from './logo.svg';
import './App.css';
import '../node_modules/font-awesome/css/font-awesome.min.css';
import axios from 'axios'
const https = require('https');

const http = axios.create({
  httpsAgent: new https.Agent({  
    rejectUnauthorized: false
  })
});

class App extends Component {

  constructor() {
    super()
    this.state = {
      'isLoading': true,
      'swimlanes': [],
      'activeSwimlaneId': null,
      'emailList': [],
      'savedState': null,
      'hasUnsavedChanges': false
    }
    this.render = this.render.bind(this)
    this.componentDidMount = this.componentDidMount.bind(this)  
    this.updateEmail = this.updateEmail.bind(this)
    this.addEmail = this.addEmail.bind(this)  
    this.setactiveSwimlaneId = this.setactiveSwimlaneId.bind(this)
    this.deleteEmail = this.deleteEmail.bind(this)
    this.hasUnsavedChanges = this.hasUnsavedChanges.bind(this)
    this.save = this.save.bind(this)
    this.setDataFromServer = this.setDataFromServer.bind(this)

    http.get("/data").then(res => {
      this.setDataFromServer(res)
    })
  }

  setDataFromServer(res) {
    this.setState({
      'isLoading': false,
      'swimlanes': res.data.swimlanes,
      'activeSwimlaneId': res.data.activeSwimlaneId,
      'emailList': res.data.emailList,
      'hasUnsavedChanges': false
    })

    this.state.savedState = JSON.parse(JSON.stringify(this.state))
  }

  componentDidMount() {
    

  }

  updateEmail(i) {
    return event => {
      this.state.emailList[i] = event.target.value
      this.reload()
    }
  }

  addEmail() {
    this.state.emailList.push("")
    this.reload()
  }

  deleteEmail(i) {
    return () => {
      this.state.emailList.splice(i, 1)
      this.reload()
    }
  }

  setactiveSwimlaneId(event) {
    this.state.activeSwimlaneId = event.target.value
    this.reload()
  }

  reload() {
    console.log(this.state)
    this.state.hasUnsavedChanges = this.hasUnsavedChanges()
    this.setState(this.state)
  }

  hasUnsavedChanges() {
    if(!this.state.savedState) {
      return false
    }

    console.log(this.state)

    for(let k in this.state) {
      if(k != 'savedState' && k != 'hasUnsavedChanges') {
        if(JSON.stringify(this.state[k]) !== JSON.stringify(this.state.savedState[k])) {
          return true
        }
      }
    }

    return false
  }

  save() {
      this.state.isLoading = true

      http.post("/data", {
        activeSwimlaneId: this.state.activeSwimlaneId,
        emailList: this.state.emailList
      }).then(res => {
        this.setDataFromServer(res)
      })
  }

  render() {
    return (
      <section className="section">
        <div className="container">
          {/* <h1 className="title">
            Hello World
          </h1> */}
          <p className="title">
            Kanban<strong>Bot</strong>
          </p>

            <form>
              <div className="field">
                <p className="control has-icons-left">
                  <span className={'select ' + (this.state.isLoading && 'is-loading') }>
                    <select value={this.state.swimlanes.map(x => x.uniqueId).includes(this.state.activeSwimlaneId) ? this.state.activeSwimlaneId : 'none'} onChange={this.setactiveSwimlaneId}>
                      <option value="none">Select Swimlane</option>
                      {
                        this.state.swimlanes.map(swimlane => (
                          <option value={swimlane.uniqueId}>{swimlane.name}</option>
                        ))
                      }
                    </select>
                  </span>
                </p>
              </div>

              

              {
                Object.keys(this.state.emailList).map(i =>  (
                  <div className="field has-addons">
                    <div className="control has-icons-left has-icons-right">
                      <input className="input" type="email" placeholder="E-mail" value={this.state.emailList[i]} onChange={this.updateEmail(i)} style={{minWidth: '400px'}} />
                    </div>
                    <p className="control">
                      <button className="button" type="button" onClick={this.deleteEmail(i)}>
                        X
                      </button>
                    </p>
                  </div>)
                )
              }

              { !this.state.isLoading && 
                <div className="field">
                  <button type="button" className="button" onClick={this.addEmail}>
                    Add Email
                  </button>
                </div>
              }

              <br/>
              <br/>
              <br/>

              { !this.state.isLoading && 
                <div className="field">
                  <button type="button" className="button is-primary" onClick={this.addEmail} disabled={!this.state.hasUnsavedChanges} onClick={this.save}>
                    Save
                  </button>
                  {/* <button type="button" className="button" onClick={this.addEmail} disabled={!this.state.hasUnsavedChanges} onClick={this.reset()}>
                    Reset
                  </button> */}
                </div>
              }
            </form>
        </div>
      </section>
    );
  }
}

export default App;



